<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">修改密码</span>
      </el-card>
    </el-row>
    <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="旧密码" prop="old_password">
        <el-col :span="8">
          <el-input type="password" v-model="ruleForm.old_password" autocomplete="off"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="新密码" prop="new_password">
        <el-col :span="8">
          <el-input type="password" v-model="ruleForm.new_password" autocomplete="off"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-col :span="8">
          <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios';
import { authName } from '@/config';
import { storageHelper } from '@/utils';
export default {
	data() {
	  var validateOldPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          callback();
        }
      };
	  var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.new_password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          old_password:'',
          new_password: '',
          checkPass: '',
        },
        rules2: {
          old_password: [
          	{ validator: validateOldPass, trigger: 'blur' }
          ],
          new_password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        }
      };
    },
    mounted() {

    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var name = storageHelper.getItem(authName, 'healthcare');
            var that = this;
            var params = {user_name:name,old_password:this.ruleForm.old_password,new_password:this.ruleForm.new_password};
            axios.post('core/user/updatePassword', params).then(function(response){
              var code = response.code;
              if(0 == code){
                that.$message({
                  type: 'success',
                  message: '修改密码成功!'
                });
              }else{
                that.$message({
                    type: 'success',
                    message: response.message
                });
              }
            },function(error){
            });
          } else {
            //console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
};
</script>

